.form-section__block {
  border-radius: 10px;
  border: 2px solid #ff9400;
  padding: 30px 85px 60px;
  margin: 43px 0 0 0;
}

.form-section__title {
  text-align: center;
  font-size: 41px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  font-family: "TimesNewRoman";
  font-weight: 400;
  margin-bottom: 14px;
}

.form-section__desc {
  text-align: center;
  font-size: 22px;
  font-family: "TimesNewRoman";
  display: block;
  max-width: 470px;
  margin: 0 auto 43px;
  position: relative;
}

.form-section__desc:before, .form-section__desc:after {
  content: '';
  display: block;
  width: 124px;
  height: 29px;
  position: absolute;
  top: 50%;
  background: url("../img/decor/form-desc-before.png") center no-repeat;
}

.form-section__desc:before {
  right: 100%;
  margin-right: 30px;
  transform: translateY(-50%);
}

.form-section__desc:after {
  transform: translateY(-50%) scale(-1, 1);
  left: 100%;
  margin-left: 30px;
}

.form-section__row {
  display: flex;
  align-items: flex-end;
  margin: -15px -15px 0;
}

.form-section .btn {
  height: 50px;
}

.form-section .field-row,
.form-section .btn {
  flex-grow: 1;
  margin: 15px;
}

@media (max-width: 991.98px) {
  .form-section__block {
    padding: 30px 20px;
    margin: 0;
  }
  .form-section__row {
    flex-wrap: wrap;
  }
  .form-section .field-row {
    width: calc(50% - 30px);
    flex-grow: inherit;
  }
  .form-section__desc:after, .form-section__desc:before {
    display: none;
  }
}

@media (max-width: 767.98px) {
  .form-section {
    padding-bottom: 0;
  }
  .form-section .field-row {
    margin-top: 0;
    width: 100%;
  }
  .form-section__desc {
    font-size: 18px;
  }
  .form-section__title {
    font-size: 36px;
    line-height: 1.2;
  }
}

@media (max-width: 329.98px) {
  .form-section__title {
    font-size: 24px;
  }
}

.reviews {
  font-size: 30px;
  font-family: "TimesNewRoman";
}

.reviews .title {
  margin-bottom: 30px;
  text-align: center;
}

.reviews .title small {
  display: block;
}

.reviews__row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -30px;
}

.reviews__col {
  width: calc(50% - 60px);
  margin: 30px;
}

.reviews img {
  display: block;
  max-width: 100%;
}

.reviews .buttons {
  margin-top: 35px;
  text-align: center;
  justify-content: center;
}

.reviews__row {
  margin: 0 -15px;
}

.reviews__col {
  width: calc(50% - 30px);
  margin: 15px;
}

@media (max-width: 767.98px) {
  .reviews__row {
    margin: 0;
  }
  .reviews__col {
    width: 100%;
    margin: 0px 0 15px 0;
  }
}

.target {
  background: url("../img/target/bg.jpg") center no-repeat;
  background-size: cover;
  padding-bottom: 10%;
  transform: skewY(11.75deg);
  transform-origin: top;
  margin-top: -10%;
  padding-top: 135px;
}

.target .container {
  transform: skewY(-11.75deg);
}

.target__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.target__media {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-top: 65px;
}

.target__media img {
  margin: 0 0 0 -40px;
  max-width: 100%;
}

.target .title {
  position: relative;
  text-align: center;
}

.target .title small {
  display: block;
  margin-bottom: 10px;
}

.target .title:after {
  content: '';
  width: 100%;
  height: 50px;
  background: url("../img/decor/title-decor.png") center no-repeat;
  background-size: contain;
  display: block;
  margin-bottom: 48px;
  margin-top: 25px;
}

.target__content {
  width: 100%;
  max-width: 562px;
  text-align: center;
}

.target__text {
  font-size: 29px;
  line-height: 1.6;
  text-transform: uppercase;
  font-family: "TimesNewRoman";
}

@media (max-width: 991.98px) {
  .target__media {
    padding-top: 0px;
  }
  .target__media img {
    margin: 0;
  }
  .target__text {
    font-size: 18px;
    padding-bottom: 60px;
  }
}

@media (max-width: 767.98px) {
  .target {
    padding-top: 80px;
    margin-bottom: -25%;
  }
  .target__text {
    font-size: 18px;
    padding-bottom: 30px;
  }
  .target__content {
    order: -1;
  }
  .target__row {
    flex-wrap: wrap;
  }
}

.percent-section {
  font-size: 30px;
  font-family: "TimesNewRoman";
}

.percent-section__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 45px;
}

.percent-section__count {
  font-size: 6.4em;
  letter-spacing: -0.03em;
  margin: 0 60px;
  text-align: center;
  position: relative;
}

.percent-section__count:after {
  content: '';
  width: 236px;
  height: 30px;
  display: block;
  background: url("../img/decor/percent-after.png") center no-repeat;
  margin: 15px auto 0;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  top: 100%;
}

.percent-section__text {
  text-align: center;
  font-style: italic;
  line-height: 1.2;
}

@media (max-width: 991.98px) {
  .percent-section {
    text-align: center;
    font-size: 24px;
    position: relative;
    z-index: 2;
  }
  .percent-section__row {
    flex-wrap: wrap;
    padding-bottom: 0;
    justify-content: center;
  }
  .percent-section__count {
    font-size: 5em;
  }
  .percent-section__count:after {
    position: relative;
    margin-bottom: 30px;
    margin-top: 0;
  }
}

.problems {
  background: linear-gradient(to bottom, #e9e9e9 0%, white 100%, white 100%);
  margin-top: -10.5%;
  padding-bottom: 0;
  margin-bottom: -45px;
}

.problems__row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -30px;
}

.problems__col {
  width: calc(33.333% - 60px);
  margin: 30px;
}

.problems .title {
  margin-bottom: 15px;
  max-width: 360px;
  display: block;
}

.problems .title small {
  display: block;
}

.problems__row {
  margin: 0 -15px;
}

.problems__col {
  width: calc(33.3333% - 30px);
  margin: 15px;
}

@media (max-width: 991.98px) {
  .problems {
    padding-bottom: 45px;
  }
  .problems__col {
    width: calc(50% - 30px);
  }
}

@media (max-width: 767.98px) {
  .problems {
    margin-bottom: 0;
    padding-bottom: 0;
  }
  .problems__row {
    margin-left: 0;
    margin-right: 0;
  }
  .problems__col {
    width: 100%;
    margin-bottom: 10px;
    margin-left: 0;
    margin-right: 0;
    margin-top: 10px;
  }
}

.card-block {
  font-size: 15px;
  text-align: center;
  box-shadow: 0 0 51px rgba(0, 0, 0, 0.09);
  border-radius: 2px;
  padding: 40px;
  background-color: rgba(255, 255, 255, 0.5);
  height: 100%;
}

.card-block__icon {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 35px;
}

.card-block__icon img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.card-block__desc {
  max-width: 235px;
  display: block;
  margin: 0 auto;
}

.card-block__title {
  font-size: 22px;
  text-transform: uppercase;
  font-family: "TimesNewRoman";
  margin-bottom: 12px;
  height: 42px;
  line-height: 1.3;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 767.98px) {
  .card-block__icon {
    margin-bottom: 15px;
  }
  .card-block__title {
    height: auto;
  }
}

.footer__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 45px 0;
}

.footer__logo {
  width: 110px;
  opacity: 0.5;
  transition: all .2s ease;
}

.footer__logo img {
  max-width: 100%;
  display: block;
}

.footer__logo:hover {
  opacity: 1;
}

.footer__bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding: 40px 0;
}

.footer__copyright {
  font-size: 19px;
}

.footer--black {
  background: #000;
  color: #fff;
}

@media (max-width: 991.98px) {
  .footer .payments {
    margin-left: auto;
  }
}

@media (max-width: 767.98px) {
  .footer__copyright {
    font-size: 14px;
    text-align: center;
  }
}

.payments ul {
  display: flex;
  align-items: center;
}

.payments li {
  margin: 0 10px;
}

.payments img {
  display: block;
}

.payments a {
  transition: all .2s ease;
  opacity: 0.4;
  display: block;
}

.payments a:hover {
  opacity: 1;
}

@media (max-width: 991.98px) {
  .payments img {
    max-height: 50px;
    max-width: 50px;
  }
}

.dva {
  background: linear-gradient(90deg, #be801e 0%, #be801e 1%, #fcd07c 100%);
}
